export function convertValue(item) {
    if (!item) return item
    const newItem = {}
    // 增加tabGroup字段
    newItem['tabGroup'] = item.friend ? 'FRIEND' : 'NPC'
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = heroFieldDef[key] ? heroFieldDef[key](value) : value
    })
    // 增加套装字段
    const suit = searchSuit(item['equip_wp'], item['equip_ar'], item['equip_pt'])
    if (suit) {
        newItem['suit'] = suit.name
        newItem['suit_bs'] = suit.bsId
        newItem['suit_fs'] = suit.fsId
        newItem['suit_gift'] = suit.giftId
    }
    // 增加武功字段
    let skillCnt = 0
    for (let i = 0; i < 30; i++ ) {
        const skill = item['skills'][i]
        if (skill['skill']) {
            const level = 1 + skill['exp'] / 100
            newItem['skill_' + (i + 1)] = `${skill['skill']}(${level}级)`
            skillCnt++
        }
    }
    newItem['skill_cnt'] = skillCnt

    // 增加物品字段
    for (let i = 0; i < 4; i++ ) {
        const it = item['items'][i]
        if (it['item']) {
            newItem['item_' + (i + 1)] = `${it['item']}*${it['amount']}`
        }
    }

    // 增加效果字段
    if (item['equip_bs']) {
        newItem['bodySkillEffect'] = searchSkillText(item['equip_bs'])['text_equip']
    }
    if (item['equip_fs']) {
        newItem['footSkillEffect'] = searchSkillText(item['equip_fs'])['text_equip']
    }
    if (item['gift']) {
        newItem['giftEffect'] = searchSkillText(item['gift'])['text_equip']
    }
    if (newItem['suit_bs']) {
        newItem['suitBodySkillEffect'] = searchSkillText(newItem['suit_bs'])['text_equip']
    }
    if (newItem['suit_fs']) {
        newItem['suitFootSkillEffect'] = searchSkillText(newItem['suit_fs'])['text_equip']
    }
    if (newItem['suit_gift']) {
        newItem['suitGiftEffect'] = searchSkillText(newItem['suit_gift'])['text_equip']
    }
    const titleData = searchTitle(item['title'])
    if (titleData) {
        newItem['titleEffect'] = titleData['desc']
        newItem['title'] = `[${item['title']}]${titleData['name']}`
    } else {
        newItem['title'] = `[${item['title']}](未定义)`
    }

    return newItem
}