import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ItemDataView from "@/views/data/ItemDataView";
import SkillDataView from "@/views/data/SkillDataView";
import SuitDataView from "@/views/data/SuitDataView";
import SkillConfigView from "@/views/data/SkillConfigView";
import HeroDataView from "@/views/data/HeroDataView";
import EventView from "@/views/event/EventView";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    },
    {
        path: '/hero',
        name: 'hero',
        component: HeroDataView
    },
    {
        path: '/item',
        name: 'item',
        component: ItemDataView
    },
    {
        path: '/skill',
        name: 'skill',
        component: SkillDataView
    },
    {
        path: '/suit',
        name: 'suit',
        component: SuitDataView
    },
    {
        path: '/skill-config',
        name: 'skill-config',
        component: SkillConfigView
    },
    {
        path: '/event',
        name: 'event',
        component: EventView
    },
    {
        path: '/coming',
        name: 'coming',
        component: () => import('../views/ComingSoon.vue')
    },
]

const router = new VueRouter({
    routes,
    mode: 'history',
    base: '/jshyl-data-web/'
})

export default router
