import {header} from "@/utils/DataTableUtil";
import {searchFriendConf, searchHero, searchItem} from "@/utils/SourceDataUtil";
import {itemType} from "@/utils/TypeValueConverter";

const friendFilter = (value, select) => {
    return {
        type: 'select',
        label: '路线',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            const friend = searchFriendConf(item.index);
            if (friend) {
                return friend.group == value
            }
            return value == '其他';
        }
    }
}
const typeFilter = (value, select) => {
    return {
        type: 'select',
        label: '类型',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            return item.op == value;
        }
    }
}

