import Vue from "vue";
import Vuex from 'vuex'
import {requestData} from "@/utils/DataRequestUtil";

Vue.use(Vuex);

function createSuitIndex(SuitSource) {
    const suitIndex = {};
    Object.values(SuitSource).forEach(suit => {
        suitIndex[suit.suitKey] = suit;
    });
    return suitIndex;
}

function createTitleIndex(TitleSource) {
    const titleIndex = {};
    Object.values(TitleSource).forEach(title => {
        titleIndex[title.skillId] = title;
    });
    return titleIndex;
}

export default new Vuex.Store({
    state: {
        HeroSource: {},
        ItemSource: {},
        SkillSource: {},
        SuitSource: {},
        SkillConfigSource: {},
        TitleConfigSource: {},
        EventSource: {},
        Const: {},

        SuitIndex: {},
        TitleIndex: {},
    },
    getters: {
    },
    mutations: {
        setHeroSource(state, data) {
            state.HeroSource = data;
        },
        setItemSource(state, data) {
            state.ItemSource = data
        },
        setSkillSource(state, data) {
            state.SkillSource = data
        },
        setSuitSource(state, data) {
            state.SuitSource = data
            state.SuitIndex = createSuitIndex(data)
        },
        setSkillConfigSource(state, data) {
            state.SkillConfigSource = data
        },
        setTitleConfigSource(state, data) {
            state.TitleConfigSource = data
            state.TitleIndex = createTitleIndex(data)
        },
        setEventSource(state, data) {
            state.EventSource = data
        },
        setConst(state, data) {
            state.Const = data
        }
    },
    actions: {
        initStore({commit}) {
            return Promise.all([
                requestData('hero').then(data => commit('setHeroSource', data)),
                requestData("item").then(res => commit("setItemSource", res)),
                requestData("skill").then(res => commit("setSkillSource", res)),
                requestData("suit").then(res => commit("setSuitSource", res)),
                requestData("skill_config").then(res => commit("setSkillConfigSource", res)),
                requestData("title_config").then(res => commit("setTitleConfigSource", res)),
                requestData("event").then(res => commit("setEventSource", res)),
                requestData("const").then(res => commit("setConst", res)),
            ])
        }
    }
})