<template>
  <v-container style="width: fit-content;min-width: 100%">
    <v-expansion-panels flat accordion v-model="option">
      <v-expansion-panel>
        <v-expansion-panel-header>
          选项
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <div v-for="filter in extraFilters" :key="filter.label">
            <v-card flat>
              <v-card-title class="text-subtitle-2">
                {{ filter.label }}
              </v-card-title>
              <v-card-text>
                <v-chip-group
                    column
                    mandatory
                    active-class="primary--text text--accent-4"
                    v-model="filter.selected"
                >
                  <v-chip outlined v-for="filterItem in filter.value" :key="filterItem" :value="filterItem" >
                    {{ filterItem }}
                  </v-chip>
                </v-chip-group>
              </v-card-text>
            </v-card>
          </div>
          <v-card flat>
            <v-card-title class="text-subtitle-2" v-if="selectedLabels.length > 0">
              表头筛选
            </v-card-title>
            <v-card-text>
              <v-chip-group
                  column
                  multiple
                  active-class="primary--text text--accent-4"
                  v-model="selectedLabels"
              >
                <v-chip outlined v-for="headerLabel in headerLabels" :key="headerLabel.value" >
                  {{ headerLabel.text }}
                </v-chip>
              </v-chip-group>
            </v-card-text>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-text-field
        class="mb-2"
        style="max-width: 100%"
        v-model="search"
        append-icon="mdi-magnify"
        label="搜索"
        single-line
        hide-details
        clearable
    ></v-text-field>
    <div style="max-width: 100%; overflow: scroll">
      <div style="width: max-content">
        <v-data-table
            dense
            multi-sort
            show-group-by
            :footer-props="{ 'items-per-page-options': [10, 20, 50, 100, -1] }"
            :calculate-widths="true"
            :headers="headers"
            :items="items"
            :items-per-page="50"
            :search="search"
            class="elevation-1 table"
        >
          <template v-for="col in searchableCol" v-slot:[`item.${col}`]="{ item }">
            <!--              <v-chip :key="col" v-if="item[`${col}`]" @click="chipClick" label outlined>-->
            <!--                {{ item[`${col}`] }}-->
            <!--              </v-chip>-->
            <a :key="col" v-if="item[`${col}`]" @click="chipClick">{{ item[`${col}`] }}</a>
          </template>
        </v-data-table>
      </div>
    </div>

  </v-container>
</template>

<script>
function dataFilterFunc(filters) {
  return (item) => {
    let result = true
    if (filters) {
      filters.forEach(filter => {
        if (filter.selected) {
          result = result && filter.filterFunc(filter.selected, item)
        }
      })
    }
    return result
  }
}

function selectLabels(headerLabels) {
  let ret = []
  headerLabels.forEach((headerLabel, index) => {
    if (headerLabel.selected) {
      ret.push(index)
    }
  })
  return ret
}

export default {
  name: "DataPage",
  data() {
    return {
      search: '',
      option: 0,
      // 筛选headerLabels中select=true的元素
      selectedLabels: selectLabels(this.headerLabels),
    };
  },
  computed: {
    headers() {
      return this.headersSource
          .filter(this.headerFilter)
          .filter(h => {
            if (h.label) {
              return this.selectedLabels.includes(this.headerLabels.findIndex(l => l.value == h.label))
            } else {
              return true
            }
          })
    },
    items() {
      return this.itemsSource.map(this.mapper).filter(this.dataFilter)
          .filter(dataFilterFunc(this.extraFilters))
    },
    searchableCol() {
      return this.headers.filter(h => h.searchable).map(h => h.value)
    }
  },
  methods: {
    chipClick(event) {
      this.search = event.target.innerText
    },
  },
  props: {
    headersSource: Array,
    itemsSource:  Array,
    headerLabels: Array,
    mapper: Function,
    headerFilter: {
      Function,
      default: () => () => true
    },
    dataFilter: {
      Function,
      default: () => () => true
    },
    extraFilters: Array,
  },
}
</script>

<style scoped>
.row {
  width: fit-content;
}
</style>